import React, {Component} from 'react';
import Projects from './Projects';
import SocialProfiles from './SocialProfiles';
import profile from './assets/images/profile.png'

class App extends Component {
    state = {displayBio: false};

    toggleDisplayBio = () => {
        this.setState({displayBio: !this.state.displayBio}); //never directly set the state, use setState
    }

    render(){

        return (
            <div>
                <img src={profile} alt='profile' className='profile'/>
                <h1>Michael Sault</h1>
                <p>H.B.Sc in Computer Science</p>
                {
                    this.state.displayBio ? (
                        <div>
                            <p1>I live in Burlington Ontario.</p1>
                            <p1>I am a recent Computer Science graduate from the University of Ottawa.</p1>
                            <p1>My preferred programming languages are Java and JS.</p1>
                            <p1>I am currently training for a half marathon.</p1>
                            <button onClick={this.toggleDisplayBio}>Show Less</button>
                        </div>
                    ) : (
                        <div>
                            <button onClick={this.toggleDisplayBio}>More About Me</button>
                        </div>
                    )
                }
                <hr />
                <Projects />
                <hr />
                <SocialProfiles />            
            </div>
        )
    }
}

export default App;