import React, {Component} from 'react';
import PROJECTS from './data/projects';

class Project extends Component {
    render() {


        const {title, image, description, link, linktext, github, ghtext, linksplit, link2, link2text} = this.props.project;


        return (
            <div style = {{display: 'inline-block', width: 400, margin: 10}}>
                <hr/>
                <h2>{title}</h2>
                <hr/>
                <img src = {image} alt='profile' style={{width: 250, height: 150}}/>
                <p1>{description}</p1>
                <a href={link}><p1>{linktext}</p1></a>
                <a href={github}><p2>{ghtext}</p2></a>
                <p2>{linksplit}</p2>
                <a href={link2}><p2>{link2text}</p2></a>
            </div>
        )
    }
}

class Projects extends Component {
    render() {
        return (
            <div>
                <h1>Highlighted Projects</h1>
                <div>
                    {
                        PROJECTS.map(PROJECT => {
                            return (
                                <Project key={PROJECT.id} project={PROJECT} />
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}

export default Projects;